:root {
    --depth: 500px;
    --turnLeft: 0.06turn;
    --turnRight: -0.06turn;
    --turn: var(--turnRight);
}

.projectsbBox {
    height: 100vh;
    overflow: hidden;
    perspective: 100vw;
    transform-style: preserve-3d;
    perspective-origin: 50% 35%;

    .room {
        position: relative;
        // border: 2vmax solid rgb(26, 26, 26);
        height: 100vh;
        width: 100vw;
        scroll-snap-align: center;
        transform-style: preserve-3d;
        border-top: 0;

        &::before,
        &::after {
            content: '';
            position: absolute;
            height: var(--depth);
            width: 100%;
            backface-visibility: hidden;
        }

        &::before {
            background:
                radial-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.1)),
                #221d29;
            transform: rotatex(-90deg) scale(1.001);
            transform-origin: center top;
            backdrop-filter: blur(10px);
        }

        &:after {
            bottom: 0;
            background:
                radial-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.1)),
                url(https://images.unsplash.com/photo-1555505019-8c3f1c4aba5f?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=3300&q=80) center / cover,
                sandybrown;
            transform: rotatex(90deg);
            transform-origin: center bottom;
        }

        .room-walls {
            position: absolute;
            inset: 0;
            background:
                linear-gradient(to top, rgb(230, 230, 230) 2.5vh, transparent 0),
                // radial-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.3)),
                url(../image/adfg.jpg),
                darkgray;
            transform: translatez(calc(var(--depth) * -1));
            transform-style: preserve-3d;

            &::before,
            &::after {
                content: '';
                position: absolute;
                height: 100%;
                width: var(--depth);
            }

            &::before {
                right: 100%;
                background:
                    linear-gradient(to top, rgb(191, 191, 191) 2.5vh, transparent 0),
                    radial-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.1)),
                    url(../image/beijin.jpg),
                    gray;
                transform: rotatey(90deg);
                transform-origin: right center;
            }

            &::after {
                left: 100%;
                background:
                    linear-gradient(to top, rgb(191, 191, 191) 2.5vh, transparent 0),
                    radial-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.1)),
                    url(../image/xiangjain.jpg),
                    // url(https://images.unsplash.com/photo-1441974231531-c6227db76b6e?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2560&q=80) center / cover,
                    gray;
                transform: rotatey(-90deg);
                transform-origin: left center;
            }
        }

        .room-content {
            width: 100%;
            height: 100%;
            position: relative;

            .content {
                position: absolute;
                left: 50%;
                top: 45.5%;
                transform: translate(-50%, -50%);
                width: 77%;
                height: 75%;
                padding: 30px;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                justify-content: space-between;

                .box {
                    border-radius: 20px;
                    width: 30%;
                    height: 240px;
                    padding: 30px;
                    background: rgba(0, 0, 0, 0.4);
                    box-shadow: inset 2px 2px 4px #fff,
                        inset -2px -2px 4px #fff;
                    backdrop-filter: blur(5px);
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;
                    color: #fff;
                    transition: all 0.5s linear;
                    &:hover{
                        transform: scale(1.2);
                        background-color: rgba(0, 0, 0, 0.8);
                    }
                    .box-title{
                        font-size: 2rem;
                    }
                    .box-name{
                        margin-top: 20px;
                        font-size: 1.4rem;
                    }
                }
            }
        }
    }
}