.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-between;


    // background-color: rgba(0, 0, 0, 0.3);
    // backdrop-filter: blur(10px);
    padding: 0 60px;
    z-index: 10 !important;

    .headerLeft {
        flex: 1;
    }

    .headerRight {
        flex: 1;
        display: flex;
        align-items: center;

        .headerRightItem {
            flex: 1;
            display: flex;
            height: 70px;
            justify-content: center;
            align-items: center;

            .btn {
                position: relative;
                top: 0;
                left: 0;
                width: 120px;
                height: 50px;
                margin: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                backdrop-filter: blur(5px);

                a {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background: rgba(255, 255, 255, 0.05);
                    box-shadow: 0 15px 15px rgba(0, 0, 0, 0.3);
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    border-top: 1px solid rgba(255, 255, 255, 0.1);
                    border-radius: 30px;
                    padding: 10px;
                    letter-spacing: 1px;
                    text-decoration: none;
                    overflow: hidden;
                    color: #fff;
                    font-weight: 400px;
                    z-index: 1;
                    transition: 0.5s;
                    backdrop-filter: blur(15px);

                    &::before {
                        content: "";
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 50%;
                        height: 100%;
                        background: linear-gradient(to left, rgba(255, 255, 255, 0.15), transparent);
                        transform: skewX(45deg) translate(0);
                        transition: 0.5s;
                        filter: blur(0px);
                    }
                }

                &:nth-child(1)::before {
                    background: #ff1f71;
                    box-shadow: 0 0 5px #ff1f71, 0 0 15px #ff1f71, 0 0 30px #ff1f71,
                        0 0 60px #ff1f71;
                }

                &:hover a {
                    letter-spacing: 3px;
                }

                &:before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    transform: translatex(-50%);
                    bottom: -5px;
                    width: 30px;
                    height: 10px;
                    background: rgb(82, 69, 141);
                    border-radius: 10px;
                    transition: 0.5s;
                    transition-delay: 0.5;
                }

                &:hover a::before {
                    transform: skewX(45deg) translate(200px);
                }

                &:hover::before

                /*lightup button*/
                    {
                    bottom: 0;
                    height: 50%;
                    width: 80%;
                    border-radius: 30px;
                }

            }
        }
    }
}