.myhokeBox{
    height: 100vh;
    padding-top: 70px;
    perspective: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(https://9to5mac.com/wp-content/uploads/sites/6/2019/07/apple_champs_elysees_boardroom.jpg?quality=82&strip=all&w=1600);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    box-sizing: border-box;
    overflow: hidden;
    color: white;
    position: relative;
    &::before{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0%;
        content: '';
        background: linear-gradient(to bottom, rgba(86, 86, 86, 0.5), rgba(0,0,0,0.5));
        animation: maymain 1.5s forwards;

    }
    @keyframes maymain {
        100% {
            height: 100%;
        }
    }
    .maymaink{
        width: 100%;
        padding: 30px;
        background-color: rgba(0,0,0,0.5);
        line-height: 30px;
        text-align: center;
        z-index: 1;
    }
}