.homeBox {
    height: 100vh;
    background-color: #061629;
    padding-top: 70px;
    overflow: hidden;

    .image {
        width: 90%;
        margin: 30px auto 0;
        height: 50%;
        overflow: hidden;
        border-radius: 10px;

        img {
            width: 100%;
            object-fit: contain;
        }
    }

    position: relative;

    .content {
        position: absolute;
        top: 55%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 80%;
        height: 75%;
        display: flex;
        align-items: center;
        justify-content: space-around;

        .contentLeft {
            box-sizing: border-box;
            height: 100%;
            width: 26%;
            background-color: rgba(12, 39, 56, 1);
            backdrop-filter: blur(5px);
            border-radius: 15px;
            // box-shadow:inset 4px 4px 10px #333, inset -4px -4px 10px #333;
            padding: 30px;
            color: #fff;

            .userimg {
                width: 150px;
                height: 150px;
                border-radius: 50%;
                overflow: hidden;
                margin: 0 auto;
                border: #fff solid 2px;
                display: flex;
                align-items: center;
                justify-content: center;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .usreList {
                margin-top: 30px;
                font-size: 20px;

                p {
                    color: #999;
                    // padding: 5px 0;

                    span {
                        font-weight: 600;
                        color: #fff;
                        font-size: 18px;

                    }
                }
            }
        }

        .contentRight {
            width: 70%;
            height: 100%;
            color: #fff;
            overflow: hidden;

            .minBox {
                height: 100%;
                width: calc(100% + 20px);
                overflow-y: scroll;
                padding-right: 20px;

                .mainTitle {
                    // margin-top: 100px;
                    font-size: 30px;
                }

                .mainList {
                    margin-top: 30px;
                    // border: #eee solid 1px;
                    padding: 20px;
                    border-radius: 20px;
                    background-color: rgba(0, 0, 0, 0.3);
                    backdrop-filter: blur(3px);
                    box-shadow: inset 0 0 10px #666;

                    .listTitle {
                        font-weight: bold;
                        font-size: 20px;
                        letter-spacing: 2px;
                    }

                    .mianItem {
                        color: #999;
                        margin-top: 15px;
                        line-height: 30px;
                        letter-spacing: 1px;
                        padding-left: 30px;
                        &:hover{
                            color: #eee;
                        }
                        span{
                            color: #fff;
                        }

                        a {
                            color: #e6a3e4;
                        }
                    }
                }
            }

            // background-color: antiquewhite;
        }

        // background-color: #fff;
        // border-radius: 10px;
        // background-color: #ecf0f3;
        // box-shadow: 4px 4px 10px #d1d9e6, -4px -4px 10px #f9f9f9;
    }
}