.experienceBox {
    height: 100vh;
    padding-top: 70px;
    perspective: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(https://9to5mac.com/wp-content/uploads/sites/6/2019/07/apple_champs_elysees_boardroom.jpg?quality=82&strip=all&w=1600);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    box-sizing: border-box;
    overflow: hidden;
    color: white;

    .card {
        width: 400px;
        padding: 20px;
        height: 200px;
        margin: 20px 0;
        background: rgba(0, 0, 0, 0.3);
        box-shadow: inset 1px 1.5px 2px #fff9, inset 1px -0.5px 2px #ffffff4d,
            0 0.6021873017743928px 0.6021873017743928px -1.25px #0000002e,
            0 2.288533303243457px 2.288533303243457px -2.5px #00000029,
            0 10px 10px -3.75px #00000010;
        backdrop-filter: blur(10px);
        border-radius: 30px;
        position: relative;

        &::before,
        &::after {
            content: "";
            position: absolute;
            height: 4px;
            width: 4px;
            bottom: -10px;
            left: 50%;
            transform: translateX(-50%);
            background: rgba(255, 255, 255, 0.4);
            border-radius: 4px;
        }

        &::after {
            margin-left: -30px;
        }

        &::before {
            width: 50px;
        }

        .card-header {
            line-height: 40px;
            padding-top: 40px;

            .card-title {
                font-size: 1.5rem;
                font-weight: 600;
                color: white;
            }

            .card-subtitle {
                font-size: 1rem;
                color: white;
            }
        }

        .center-box {
            padding: 30px;
            .center-item {
                &:nth-child(1) {
                    font-size: 1.5rem;
                    font-weight: 600;
                    color: white;
                }

                &:nth-child(2) {
                    font-size: 1rem;
                    color: white;
                    margin-top: 20px;
                }

                &:nth-child(3) {
                    margin-top: 15px;
                }

                &:nth-child(4) {
                    margin-top: 25px;
                    line-height: 30px;
                }
            }
        }

    }

    .side {
        display: flex;
        flex-direction: column;
        flex: 1;
        height: 100%;
        align-items: center;
        justify-content: center;
        transition: transform 0.5s;
        // background-color: red;
    }

    .left-side {
        padding-left: 60px;
        transform: rotateY(45deg) translateX(-1000px);
        animation: animate-left 1s forwards;

        .card {
            @extend .card;
        }
    }

    .right-side {
        transform: rotateY(-45deg) translateX(1000px);
        animation: animate-right 1s forwards;
        padding-right: 60px;

        .card {
            @extend .card;
            width: 90%;
            height: 100%;
        }
    }

    .center-side {
        transform: rotateX(0deg) scale(0.9) translateY(-1000px);
        animation: animate-center 0.7s forwards;

        .card {
            @extend .card;
            width: 100%;
            height: 100%;

        }
    }

    @keyframes animate-left {
        100% {
            transform: rotateY(45deg) translateX(0px);
        }
    }

    @keyframes animate-right {
        100% {
            transform: rotateY(-45deg) translateX(0px);
        }
    }

    @keyframes animate-center {
        100% {
            transform: rotateX(0deg) scale(0.9) translateY(0px);
        }
    }


}